<template>
    <div class="container mx-auto p-4">
        <header class="text-center my-8">
            <h1 class="text-3xl font-bold mb-4">Edit Profile</h1>
        </header>


        <!-- 使用全局注册的 BackHome 组件 -->
        <BackHome />
        <div class="toast" v-if="isAlertVisible">
            <div class="alert alert-info">
                <span>修改成功</span>
            </div>
        </div>

        <div class="card bg-base-100 shadow-xl p-6">
            <div class="form-control mb-4">
                <label class="label">Name</label>
                <input v-model="formData.Byname" type="text" placeholder="Enter your name"
                    class="input input-bordered" />
            </div>
            <div class="form-control mb-4">
                <label class="label">Email</label>
                <input v-model="formData.Byemail" type="email" placeholder="Enter your email"
                    class="input input-bordered" />
            </div>
            <button @click="updateProfile" class="btn btn-primary mt-4">Update Profile</button>
        </div>
    </div>
</template>

<script> 
import { selAPI, updataAPI } from "@/archive/user.js";

export default {
    name: 'EditProfile',
    data() {
        return {
            formData: {
                Byname: '',
                Byemail: '',
            },
            isAlertVisible: false, // 控制 alert 是否显示
        };
    },
    components: {  },
    async created() {
        // Fetch the current user data (replace with actual user ID)
        const data = await selAPI('8e4e23fd-4f60-4373-aa2c-a9e905d119f7');
        if (data) {
            this.formData = data;
        }
    },
    methods: {
        // Handle the profile update when button is clicked
        async updateProfile() {
            const { data, error } = await updataAPI('8e4e23fd-4f60-4373-aa2c-a9e905d119f7',
                {
                    Byname: this.formData.Byname,
                    Byemail: this.formData.Byemail,
                }
            );

            if (error) {
                console.error('Error updating profile:', error);
            } else {
                // 成功后显示 DaisyUI 的 alert
                this.isAlertVisible = true;
                setTimeout(() => {
                    this.isAlertVisible = false; // 2秒后自动隐藏提示框
                    this.$router.push({ name: 'HelloWorld' }); // Redirect after successful update
                }, 1000);
            }
        },
    },
};
</script>
