        <h3 class="card-title mb-2">编辑个人资料</h3>
        <p class="mb-4">更新您的个人信息和偏好</p>
        <button class="btn btn-primary" @click="editProfile">Edit</button>
      </div>
      <div class="card bg-base-100 shadow-lg p-4">
        <h3 class="card-title mb-2">更改密码</h3>
        <p class="mb-4">定期更新密码，以确保您的帐户安全</p>
        <button class="btn btn-secondary" @click="changePassword">Change Password</button>
      </div>
    </section>

    <!-- Blog Posts -->
    <main class="newacer grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6 mt-8">
      <div v-for="post in posts" :key="post.id" class="card bg-base-100 image-full w-96 shadow-xl">
        <figure>
          <img :src="post.image" alt="Blog post image" class="rounded-t-lg" />
        </figure>
        <div class="card-body">
          <h2 class="card-title">{{ post.title }}</h2>
          <p class="line-clamp-3">{{ post.content }}</p>
          <div class="card-actions justify-end">
            <button class="btn btn-primary" @click="viewPost(post.art_uuid, user.Byname)">阅读更多</button>
            <button class="btn btn-ghost" @click="getIddel(post.art_uuid, user.Byid)">Del</button>
          </div>
        </div>
