<script>

import { supaAPI, insetAPI, selAPI, delAPI, updataAPI, selArtAPI, DelartAPI } from "@/archive/user.js";
import { DateTime } from "@/archive/dates.js";
import { faker } from '@faker-js/faker';



// const userdata = {
//   Byid: faker.string.uuid(),
//   Byname: faker.name.fullName(),
//   Byavatar: faker.image.avatar(),
//   Byemail: faker.internet.email(),
//   Bynl: faker.location.city(),
//   Byah: faker.location.country(),
//   Bydeta: DateTime
// }
// supaAPI(userdata)
// insetAPI(userdata)
// delAPI('da49a8b6-8bd2-4c1d-8e5a-01beaf2cb7f2')
// updataAPI('0c50b11c-529b-47c3-9152-88717a2b1523', {
//   Byname: "this.formData.Byname",
//   Byemail: "55555@qq.com",
// })
// selAPI('0c50b11c-529b-47c3-9152-88717a2b1523')


export default {
  name: 'HelloWorld',
  data() {
    return {
      user: null,
      posts: [],
      isAlertVisible: false, // 控制提示框是否显示
      isloadings: true, // 控制提示框是否显示
      userload: true,
      art_uuid: null,
      author_Byid: null
    };
  },
  methods: {
    viewPost(art_uuid, Byname) {
      // console.log(art_uuid,author_Byid,'文章uuid，用户uuid');

      this.$router.push({
        name: 'ArticleView', params: {
          art_uuid,
          Byname
        }
      });
    },
    editProfile() {
      this.$router.push({ name: 'EditProfile' });
    },
    changePassword() {
      this.$router.push({ name: 'UpdatePassword' });
    },
    getIddel(art_uuid, author_Byid) {

      this.art_uuid = art_uuid
      this.author_Byid = author_Byid
      my_modal_1.showModal()

    },
    addarticle() {  //发布文章：传用户id 
      this.$router.push({ name: 'AddArticle', params: { articleId: this.user.Byid } })
    },
    async Delart() {
      const data = await DelartAPI(this.art_uuid, this.author_Byid)

      if (data) {
        // 成功后显示 DaisyUI 的 alert 
        this.posts = data
        this.isAlertVisible = true;
        setTimeout(() => {
          this.isAlertVisible = false; // 2秒后自动隐藏提示框 
        }, 1000);
      }
    }

  },
  async created() {

    try {
      // 可能会出现错误的代码 
      const data = await selAPI('8e4e23fd-4f60-4373-aa2c-a9e905d119f7')
      const artdata = await selArtAPI('8e4e23fd-4f60-4373-aa2c-a9e905d119f7')


      this.userload = true
      setTimeout(() => {
        this.user = data
        this.posts = artdata
        this.isloadings = false
        this.userload = false
        console.log(this.posts, 'po');
      }, 1000);
    } catch (error) {
      console.error('出现错误：', error);
      // 可以进行一些错误处理，而不是让错误显示在页面上
    }


  },
};
</script>

